<template>
  <div id="login">
    <h1>Let's share</h1>
    <el-form
      :model="loginForm"
      :rules="rules2"
      ref="loginForm"
      label-width="0"
      class="demo-ruleForm"
    >
      <el-form-item prop="username">
        <el-input
          placeholder="用户名"
          type="username"
          v-model="loginForm.username"
          auto-complete="off"
        ></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input placeholder="密码" type="password" v-model="loginForm.password" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onLogin">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// import { mapActions } from "vuex";
import axios from "axios";
import qs from "qs";

//post header type
// axios.defaults.headers.post["Content-Type"] = "application/x-www-form-urlencoded";
axios.defaults.headers.post["Content-Type"] = "application/json";
// axios.defaults.headers.post["Content-Type"] = "multipart/form-data;charset=utf-8";

// backend interface
axios.defaults.baseURL = "http://192.168.193.16:5000";
// axios.defaults.baseURL = "http://192.168.195.10:5005";

// cookie
axios.defaults.withCredentials = false;


export default {
  data() {
    var validateUser = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入用户名"));
      } else {
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        username: "",
        password: ""
      },
      rules2: {
        username: [{ validator: validateUser, trigger: "blur" }],
        password: [{ validator: validatePass, trigger: "blur" }]
      }
    };
  },
  methods: {
    onLogin() {
      console.log(this.loginForm.username, this.loginForm.password)
      axios
        .post(
          "/login",
          {
            user_account: this.loginForm.username,
            user_password: this.loginForm.password
          },
          {
            headers: {
              token: window.localStorage.getItem("token") //由于是多页面应用所以token存储在本地localStorage中
            }
          }
        )
        .then(res => {
          console.log("response", res);
          if (res.data.status == "200") {
            this.$router.push({ path: this.$route.query.redirect || "/" });
            // resolve(res.data);
          } else {
            this.$message.error(res.data.msg);
            // reject(res.data);
          }
        })
        .catch(err => {
          this.$message.error("网络异常");
          // reject({ msg: "网络异常" });
        });
    }

    // ...mapActions(["login"]),

    // onLogin() {
    //   this.login({
    //     user_account: this.loginForm.username,
    //     user_password: this.loginForm.password
    //   }).then(() => {
    //     this.$router.push({ path: this.$route.query.redirect || "/" });
    //   });
    // console.log(
    //   "info: " + this.loginForm.username + ":" + this.loginForm.password
    // );
    // }

    // submitForm(formName) {
    //   this.$refs[formName].validate(valid => {
    //     if (valid) {
    //       this.$message.success("请求合法");
    //     } else {
    //       this.$message.error("请求不合法!");
    //       setTimeout(() => {
    //         return false;
    //       }, 500);
    //     }
    //   });
    // }
  }
};
</script>

<style scoped>
/* common */
#login {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* height: 40vh; */
}
#login h1 {
  line-height: 3;
}
</style>



