<template>
  <div id="detail">
    <section class="left">
      <section class="article-wrapper">
        <h1 class="article-title">文章标题</h1>
        <div class="article-info">
          <router-link to="/user" class="author">bob</router-link>
          <span class="time">2019-11-20 18:57:00</span>
          <span class="view-num">
            <i class="el-icon-view"></i>
            阅读 215
          </span>
          <ul class="tag-list">
            <li class="tag-item"></li>
            <li class="tag-item"></li>
            <li class="tag-item"></li>
          </ul>
        </div>
        <article>
          <p>我是一个特别爱惜时间的人。</p>
          <p>每天5:30起床，6:15到图书馆，这样的作息时间几乎持续了将近两年的光阴。</p>
          <p>后来，我对自己时间的把控越来越精准。</p>
          <p>如果有一天，我不小心睡过了头，那么我甚至可以准确预估出和平时相比，我会迟几分钟出现在图书馆。</p>
          <p>过去一年的考研时光，我将自己每月、每周、每天的安排都按照小时来划分。</p>
          <p>特别是每天的安排，我会根据平时学校的课程表画出一条时间刻度线，然后分成几个部分，将每天的任务分别对应到具体的时间段。</p>
          <p>甚至精确到从图书馆到上课班级，我需要花费多长的时间跑过去；下课结束之后，回到图书馆，我需要再看多长时间的书；午休休息多长时间，下午几点开始学习；晚上几点去吃晚饭.....</p>
          <p>年轻的时候，就要和时间进行赛跑，你对待时间的态度决定你生活的态度。</p>
        </article>
        <div class="favor">
          <i class="icon-ali-favor" id="icon-favor" @click="favor">&nbsp;赞 {{favorNum}}</i>
          <button @click="follow">关注</button>
        </div>
        <!-- <div class="reward"></div> -->
      </section>
      <section class="comment-wrapper">
        <div class="comment-list">
          <comment />
          <comment />
          <comment />
          <comment class="last-comment-item" />
        </div>
      </section>
    </section>
    <section class="card"></section>
  </div>
</template>

<script>
import comment from "@/components/Comment";
export default {
  data() {
    return { favorNum: 10 };
  },
  methods: {
    favor() {
      this.favorNum += 1;
    },
    follow() {
      this.$message.success("关注成功");
      setTimeout(() => {
        this.$router.push("/user");
      }, 1000);
    }
  },
  components: {
    comment
  }
};
</script>

<style scoped>
/* common */
#detail {
  padding-bottom: 100px;
  text-align: left;
  display: flex;
}

section.article-wrapper,
section.comment-wrapper {
  display: flex;
  flex-direction: column;
  margin: 30px 0;
  padding: 30px 40px;
  /* width: 800px; */
  box-shadow: 0 0 4px 0 #d0d0d0;
}
section.comment-wrapper {
  padding: 10px 40px 30px 40px;
}

section.article-wrapper h1 {
  font-size: 30px;
  /* line-height: 3; */
}
section.article-wrapper .article-info {
  margin-bottom: 20px;
  color: #d0d0d0;
  font-size: 14px;
}
section.article-wrapper .article-info span {
  padding-left: 10px;
}
section.article-wrapper .article-info .author:hover {
  color: #81d4fa80;
}
section.article-wrapper article p {
  margin: 10px 0;
}
section.article-wrapper .favor {
  margin: 20px 0;
}
section.article-wrapper .favor i {
  border: 1px solid #e6e6e6;
  margin-right: 20px;
  padding: 5px 20px;
  font-size: 14px;
  display: inline-block;
  border: 1px solid #81d4fa;
  border-radius: 20px;
  color: #81d4fa;
  user-select:none;
}
section.article-wrapper .favor i:hover {
  background: #81d4fa30;
  cursor: pointer;
}
section.article-wrapper .favor button {
  width: 70px;
  height: 40px;
  padding: 0 10px;
  background: #fff;
  border: 1px solid #81d4fa;
  border-radius: 20px;
  color: #81d4fa;
  transition: 0.4s;
}
section.article-wrapper .favor button:hover {
  background: #81d4fa30;
  cursor: pointer;
}
section.article-wrapper .favor button:focus {
  outline: none;
}

/* diy element ui */
section.comment-wrapper .el-button {
  height: 30px;
  padding: 0 20px;
}

section.comment-wrapper .last-comment-item {
  border-bottom: 0;
}
</style>



