import request from '@/helpers/request'

const URL = {
  LOGIN: '/login',
  LOGOUT: '/logout',
  GET_INFO: '/'
}

export default {
  login({ user_account, user_password }) {
    return request(URL.LOGIN, 'POST', { user_account, user_password })
  },

  logout() {
    return request(URL.LOGOUT)
  },

  getInfo() {
    return request(URL.GET_INFO)
  }
}

