<template>
  <div id="admin-login">
    <h1>admin</h1>
    <el-form
      :model="aloginForm"
      :rules="rules2"
      ref="aloginForm"
      label-width="0"
      class="demo-ruleForm"
    >
      <el-form-item prop="account">
        <el-input placeholder="管理员" type="username" v-model="aloginForm.account" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item prop="passwd">
        <el-input
          placeholder="密码"
          type="password"
          v-model="aloginForm.passwd"
          auto-complete="off"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onLogin">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入账号"));
      } else {
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    return {
      aloginForm: {
        account: "",
        passwd: ""
      },
      rules2: {
        account: [{ validator: validatePass, trigger: "blur" }],
        passwd: [{ validator: validatePass2, trigger: "blur" }]
      }
    };
  },
  methods: {
    onLogin() {
      this.$router.push("/setUser");
      // axios
      //   .post(
      //     "/ad/login",
      //     {
      //       admin_name: this.account,
      //       password: this.passwd
      //     },
      //     {
      //       headers: {
      //         token: window.localStorage.getItem("token") //由于是多页面应用所以token存储在本地localStorage中
      //       }
      //     }
      //   )
      //   .then(res => {
      //     console.log(
      //       "info: ",
      //       this.loginForm.account,
      //       this.loginForm.passwd
      //     );
      //     console.log("data", data);
      //     if (res.data.status == "200") {
      //       this.$router.push({ path: this.$route.query.redirect || "/setUser" });
      //       // resolve(res.data);
      //     } else {
      //       this.$message.error(res.data.msg);
      //       // reject(res.data);
      //     }
      //   })
      //   .catch(err => {
      //     this.$message.error("网络异常");
      //     reject({ msg: "网络异常" });
      //   });
    }
  }
};
</script>

<style scoped src="./template.css">
</style>



